import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { SpecialApplicationsComponent } from './special-applications.component';

const routes: Routes = [
  {
    path: '',
    component: SpecialApplicationsComponent
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class SpecialApplicationsRoutingModule { }
